}

.header-center a {
  position: relative;
  top: 87vh;
  left: 66vw;
  font-family: 'VT323', monospace;
  font-size: 36pt;
  color: white;
  opacity: 0.4;
}

.header-center:hover a {
  cursor: pointer;
  opacity: 0.6;
  text-decoration: none;
  -webkit-transition: opacity 200ms ease-in-out;
  -o-transition: opacity 200ms ease-in-out;
  transition: opacity 200ms ease-in-out;
}

.header-right {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  position: absolute;
  margin: 15% 0 0 86%;
  -webkit-transform: rotate(3deg);
      -ms-transform: rotate(3deg);
          transform: rotate(3deg);
}

.github-icon a,
.linkedin-icon a,
.gmail-icon a {
  fill: #fff;
  opacity: 0.3;
  padding-top: 40px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  color: white;
}

.github-icon:hover a,
.linkedin-icon:hover a,
.gmail-icon:hover a {
  cursor: pointer;
  fill: #fff;
  opacity: 0.4;
  text-decoration: none;
  -webkit-transition: 200ms ease-in-out;
  -o-transition: 200ms ease-in-out;
  transition: 200ms ease-in-out;
}

.github-text,
.linkedin-text,
.gmail-text {
  visibility: hidden;
  font-family: 'VT323', monospace;
  font-size: 19pt;
}

@-webkit-keyframes github-slide {
  0%, 50% { opacity: 0; }
  100% { -webkit-transform: translateX(-250%); transform: translateX(-250%); visibility: visible; }
}

@keyframes github-slide {
  0%, 50% { opacity: 0; }
  100% { -webkit-transform: translateX(-250%); transform: translateX(-250%); visibility: visible; }
}

@-webkit-keyframes linkedin-email-slide {
  0%, 50% { opacity: 0; }
  100% { -webkit-transform: translateX(-220%); transform: translateX(-220%); visibility: visible; }
}

@keyframes linkedin-email-slide {
  0%, 50% { opacity: 0; }
  100% { -webkit-transform: translateX(-220%); transform: translateX(-220%); visibility: visible; }
}

.github-icon:hover .github-text {
  -webkit-animation: github-slide 500ms ease-in-out forwards;
          animation: github-slide 500ms ease-in-out forwards;
}

.linkedin-icon:hover .linkedin-text,
.gmail-icon:hover .gmail-text {
  -webkit-animation: linkedin-email-slide 500ms ease-in-out forwards;
          animation: linkedin-email-slide 500ms ease-in-out forwards;
}
