.show {
  height: 545px;
  visibility: visible;
  /*transition: all 1000ms ease-in-out;*/
}

.hide,
.hide form,
.hide input[type='text'],
.hide textarea,
.hide button {
  position: absolute;
  height: 0;
  top: -9999px;
  left: -9999px;
  visibility: hidden;
  -webkit-transition: all 1000ms ease-in-out;
  -o-transition: all 1000ms ease-in-out;
  transition: all 1000ms ease-in-out;
}

