.work-history-section {
  background-color: #333;
  color: #FAFAFA;
}

.work {
  padding: 25px;
}

.work-title {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 2.7em;
  padding-bottom: 20px;
  /*padding: 25px;*/
}

.work-title ~ h4 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 1.7em;
  padding-top: 10px;
}

.work-title ~ p {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.1em;
  font-weight: 400;
}

.work-list {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.1em;
  font-weight: 400;
}

.work-list li {
  margin: 0 0 7px 0;
}

.quote-footer {
  background-color: #222;
  color: #FAFAFA;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  font-size: 1.3em;
  text-align: center;
  /*height: 76px;*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding: 20px;
}

.quote-author {
  font-size: 0.8em;
  padding-top: 5px;
}


